.footer {
	max-width: calc(100% - 140px);
	padding: 59px 70px 20px;
	display: flex;
	flex-direction: column;
	justify-content: space-between;
	align-items: center;
	background-color: #fafafa;
	position: relative;
}

.footer__title {
	width: 100%;
	margin: 0;
	padding: 20px 0 20px;
	border-bottom: 1px solid #e8e8e8;
	color: #a0a0a0;
	font-family: Inter;
	font-style: normal;
	font-weight: normal;
	font-size: 16px;
	line-height: 16px;
	text-align: center;
}

.footer__conteiner {
	width: 100%;
	padding-top: 20px;
	display: flex;
	justify-content: space-between;
	align-items: baseline;
}

.footer__copyright {
	margin: 0;
	font-family: Inter;
	font-style: normal;
	font-weight: normal;
	font-size: 16px;
	line-height: 16px;
}

.footer__links {
	list-style-type: none;
	padding: 0;
	margin: 0;
	display: flex;
	align-items: center;
	column-gap: 20px;
}

.footer__link {
	text-decoration: none;
	color: inherit;
	font-family: Inter;
	font-style: normal;
	font-weight: normal;
	font-size: 16px;
	line-height: 16px;
}

.footer__link:hover {
	opacity: 0.8;
}

@media screen and (max-width: 1023px) {
	.footer {
		max-width: calc(100% - 60px);
		padding: 79px 30px 20px;
		margin: 0 auto;
	}
}

@media screen and (max-width: 767px) {
	.footer {
		max-width: calc(100% - 20px);
		padding: 79px 10px 10px;
	}

	.footer__title {
		padding: 21px 0 21px;
		font-size: 16px;
		line-height: 15px;
	}

	.footer__conteiner {
		flex-direction: column-reverse;
		align-items: center;
	}

	.footer__copyright {
		font-size: 16px;
		line-height: 15px;
	}

	.footer__link {
		font-size: 16px;
		line-height: 15px;
	}

	.footer__links {
		padding: 30px 0;
		flex-direction: column;
		row-gap: 12px;
	}
}
