.portfolio {
  max-width: calc(100% - 140px);
  padding: 0 70px 125px;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  background-color: #ffffff;
}

.portfolio__title {
  margin: 0;
  padding-top: 60px;
  padding-bottom: 30px;
  font-family: Inter;
  font-style: normal;
  font-weight: normal;
  font-size: 30px;
  line-height: 20px;
  color: #a0a0a0;
}

.portfolio__title:first-child {
  padding-top: 0;
}

.portfolio__links {
  list-style-type: none;
  padding: 0;
  margin: 0;
  display: flex;
  flex-direction: column;
  align-items: center;
}

.portfolio_item {
  width: 100%;
}

.portfolio__link {
  text-decoration: none;
  font-family: Inter;
  font-style: normal;
  font-weight: normal;
  font-size: 18px;
  line-height: 50px;
}

.portfolio__link:hover {
  opacity: 0.8;
}

.portfolio__link-button {
  width: 100%;
  padding: 20px 0;
  display: flex;
  justify-content: space-between;
  flex-wrap: wrap;
  border: none;
  border-bottom: 1px solid #e8e8e8;
  background-color: #ffffff;
  color: #000000;
  cursor: pointer;
}

.portfolio__arrow {
  width: 20px;
  height: 20px;
  fill: currentColor;
}

.portfolio__noborder {
  border-bottom: none;
}

@media screen and (max-width: 1023px) {
  .portfolio {
    max-width: calc(100% - 100px);
    padding: 0 50px 90px;
    margin: 0 auto;
  }

  .portfolio__link-button {
    padding-bottom: 19px;
  }

  .portfolio__title {
    font-size: 28px;
  }

  .portfolio__arrow {
    width: 27px;
    height: 60px;
  }
}

@media screen and (max-width: 767px) {
  .portfolio {
    max-width: calc(100% - 28px);
    padding: 0 14px 70px;
  }

  .portfolio__link-button {
    padding-bottom: 19px;
  }

  .portfolio__title {
    font-size: 16px;
    line-height: 28px;
  }

  .portfolio__arrow {
    width: 18px;
    height: 28px;
  }

  .portfolio__title {
    padding-bottom: 20px;
    font-size: 18px;
  }
}
