.sites {
  max-width: calc(100% - 140px);
  padding: 110px 70px 100px;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  background-color: #ffffff;
}

.sites__title {
  max-width: 100%;
  margin: 0;
  padding-bottom: 23px;
  border-bottom: 1px solid #000000;
  font-family: Inter;
  font-style: normal;
  font-weight: normal;
  font-size: 22px;
  line-height: 27px;
}

.sites__conteiner {
  padding-top: 66px;
  display: flex;
  flex-direction: row;
  column-gap: 270px;
}

.sites__profile {
  display: flex;
  flex-direction: column;
  justify-content: space-around;
}

.sites__name {
  margin: 0;
  padding-bottom: 18px;  
  font-family: Inter;
  font-style: normal;
  font-weight: normal;
  font-size: 50px;
  line-height: 58px;
}

.sites__description {
  margin: 0;
  padding-bottom: 26px;
  font-family: Inter;
  font-style: normal;
  font-weight: 500;
  font-size: 18px;
  line-height: 20px;
  line-height: 150%;
}

.sites__info {
  margin: 0;
  /* padding-bottom: 99px; */
  font-family: Inter;
  font-style: normal;
  font-weight: normal;
  font-size: 16px;
  line-height: 22px;
}

.sites__links {
  list-style-type: none;
  padding: 0;
  margin: 0;
  display: flex;
  align-items: center;
}

.sites_item {
  margin-right: 20px;
}

.sites__link {
  text-decoration: none;
  color: inherit;
  font-family: Inter;
  font-style: normal;
  font-weight: 500;
  font-size: 16px;
  line-height: 17px;
}

.sites__link:hover {
  opacity: 0.8;
}
.sites__img {
  width: 270px;
  height: 327px;
  border-radius: 10px;
  object-fit: cover;
  object-position: center;
}

@media screen and (max-width: 1023px) {
  .sites {
    max-width: calc(100% - 100px);
    padding: 90px 50px 91px;
    margin: 0 auto;
  }

  .sites__name {
    padding-bottom: 16px;
    font-size: 40px;
    line-height: 40px;
  }

  .sites__description {
    padding-bottom: 20px;
    font-weight: 500;
    font-size: 16px;
    line-height: 18px;
  }

  .sites__info {
    /* padding-bottom: 87px; */
    font-size: 16px;
    line-height: 18px;
  }

  .sites__conteiner {
    column-gap: 49px;
  }

  .sites__img {
    width: 255px;
    height: 307px;
  }
}

@media screen and (max-width: 767px) {
  .sites {
    max-width: calc(100% - 28px);
    padding: 70px 14px 69px;
  }

  .sites__name {
    padding-bottom: 20px;
    font-size: 30px;
    line-height: 36px;
  }

  .sites__description {
    font-size: 16px;
    line-height: 16px;
  }

  .sites__info {
    /* padding-bottom: 87px; */
    font-size: 16px;
    line-height: 18px;
  }

  .sites__conteiner {
    padding-top: 60px;
    display: flex;
    flex-direction: column-reverse;
    justify-content: space-between;
    align-items: center;
    row-gap: 40px;
  }

  .sites__img {
    width: 292px;
    height: 352px;
  }

  .sites__title {
    padding-bottom: 28px;
    font-size: 18px;
    line-height: 22px;
  }

  .sites__info {
    padding-bottom: 40px;
    font-size: 16px;
    line-height: 16px;
  }
}
