.pos {
  max-width: calc(100% - 140px);
  padding: 110px 70px 100px;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  background-color: #ffffff;
}

.pos__title {
  max-width: 100%;
  margin: 0;
  padding-bottom: 23px;
  border-bottom: 1px solid #000000;
  font-family: Inter;
  font-style: normal;
  font-weight: normal;
  font-size: 22px;
  line-height: 27px;
}

.pos__conteiner {
  padding-top: 66px;
  display: flex;
  flex-direction: row;
  column-gap: 270px;
}

.pos__profile {
  display: flex;
  flex-direction: column;
  justify-content: space-around;
}

.pos__name {
  margin: 0;
  padding-bottom: 18px;
  font-family: Inter;
  font-style: normal;
  font-weight: normal;
  font-size: 50px;
  line-height: 58px;
}

.pos__description {
  margin: 0;
  padding-bottom: 26px;
  font-family: Inter;
  font-style: normal;
  font-weight: 500;
  font-size: 18px;
  line-height: 20px;
  line-height: 150%;
}

.pos__info {
  margin: 0;
  /* padding-bottom: 99px; */
  font-family: Inter;
  font-style: normal;
  font-weight: normal;
  font-size: 16px;
  line-height: 22px;
}

.pos__links {
  list-style-type: none;
  padding: 0;
  margin: 0;
  display: flex;
  align-items: center;
}

.pos_item {
  margin-right: 20px;
}

.pos__link {
  text-decoration: none;
  color: inherit;
  font-family: Inter;
  font-style: normal;
  font-weight: 500;
  font-size: 16px;
  line-height: 17px;
}

.pos__link:hover {
  opacity: 0.8;
}
.pos__img {
  width: 270px;
  height: 327px;
  border-radius: 10px;
  object-fit: cover;
  object-position: center;
}

@media screen and (max-width: 1023px) {
  .pos {
    max-width: calc(100% - 100px);
    padding: 90px 50px 91px;
    margin: 0 auto;
  }

  .pos__name {
    padding-bottom: 16px;
    font-size: 40px;
    line-height: 40px;
  }

  .pos__description {
    padding-bottom: 20px;
    font-weight: 500;
    font-size: 16px;
    line-height: 18px;
  }

  .pos__info {
    /* padding-bottom: 87px; */
    font-size: 16px;
    line-height: 18px;
  }

  .pos__conteiner {
    column-gap: 49px;
  }

  .pos__img {
    width: 255px;
    height: 307px;
  }
}

@media screen and (max-width: 767px) {
  .pos {
    max-width: calc(100% - 28px);
    padding: 70px 14px 69px;
  }

  .pos__name {
    padding-bottom: 20px;
    font-size: 30px;
    line-height: 36px;
  }

  .pos__description {
    font-size: 16px;
    line-height: 16px;
  }

  .pos__info {
    /* padding-bottom: 87px; */
    font-size: 16px;
    line-height: 18px;
  }

  .pos__conteiner {
    padding-top: 60px;
    display: flex;
    flex-direction: column-reverse;
    justify-content: space-between;
    align-items: center;
    row-gap: 40px;
  }

  .pos__img {
    width: 292px;
    height: 352px;
  }

  .pos__title {
    padding-bottom: 28px;
    font-size: 18px;
    line-height: 22px;
  }

  .pos__info {
    padding-bottom: 40px;
    font-size: 16px;
    line-height: 16px;
  }
}

.portfolio-pos {
  max-width: calc(100% - 140px);
  padding: 0 70px 125px;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  background-color: #ffffff;
}

.portfolio-pos__title {
  margin: 0;
  padding-top: 60px;
  padding-bottom: 30px;
  font-family: Inter;
  font-style: normal;
  font-weight: normal;
  font-size: 30px;
  line-height: 20px;
  color: #a0a0a0;
}

.portfolio-pos__title:first-child {
  padding-top: 0;
}

.portfolio-pos__links {
  list-style-type: none;
  padding: 0;
  margin: 0;
  display: flex;
  flex-direction: column;
  align-items: center;
}

.portfolio-pos_item {
  width: 100%;
}

.portfolio-pos__link {
  text-decoration: none;
  font-family: Inter;
  font-style: normal;
  font-weight: normal;
  font-size: 18px;
  line-height: 50px;
}

.portfolio-pos__link:hover {
  opacity: 0.8;
}

.portfolio-pos__link-button {
  width: 100%;
  padding: 20px 0;
  display: flex;
  justify-content: space-between;
  flex-wrap: wrap;
  border: none;
  border-bottom: 1px solid #e8e8e8;
  background-color: #ffffff;
  color: #000000;
  cursor: pointer;
}

.portfolio-pos__arrow {
  width: 20px;
  height: 20px;
  fill: currentColor;
}

.portfolio-pos__noborder {
  border-bottom: none;
}

@media screen and (max-width: 1023px) {
  .portfolio-pos {
    max-width: calc(100% - 100px);
    padding: 0 50px 90px;
    margin: 0 auto;
  }

  .portfolio-pos__link-button {
    padding-bottom: 19px;
  }

  .portfolio-pos__title {
    font-size: 28px;
  }

  .portfolio-pos__arrow {
    width: 27px;
    height: 60px;
  }
}

@media screen and (max-width: 767px) {
  .portfolio-pos {
    max-width: calc(100% - 28px);
    padding: 0 14px 70px;
  }

  .portfolio-pos__link-button {
    padding-bottom: 19px;
  }

  .portfolio-pos__title {
    font-size: 16px;
    line-height: 28px;
  }

  .portfolio-pos__arrow {
    width: 18px;
    height: 28px;
  }

  .portfolio-pos__title {
    padding-bottom: 20px;
    font-size: 18px;
  }
}
